// router/index.js
import Vue from 'vue';
import VueRouter from 'vue-router';
import InicioComponent from '@/components/InicioComponent.vue';
import LoginComponent from '@/components/LoginComponent.vue';
import RegistroComponent from '@/components/RegistroComponent.vue';
import ListarTareasComponent from '@/components/ListarTareasComponent.vue';
import AgregarComponent from '@/components/AgregarComponent.vue';
import EditarComponent from '@/components/EditarComponent.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Inicio',
    component: InicioComponent
  },
  {
    path: '/login',
    name: 'Login',
    component: LoginComponent
  },
  {
    path: '/registro',
    name: 'Registro',
    component: RegistroComponent
  },
  {
    path: '/tareas',
    name: 'Tareas',
    component: ListarTareasComponent,
    meta: { requiresAuth: true }
  },
  {
    path: '/agregar',
    name: 'Agregar',
    component: AgregarComponent,
    meta: { requiresAuth: true }
  },
  {
    path: '/editar/:id',
    name: 'Editar',
    component: EditarComponent,
    meta: { requiresAuth: true }
  }
];

const router = new VueRouter({
  mode: 'history',
  routes
});

router.beforeEach((to, from, next) => {
  const isAuthenticated = !!localStorage.getItem('token');
  if (to.matched.some(record => record.meta.requiresAuth) && !isAuthenticated) {
    next({ name: 'Login' });
  } else {
    next();
  }
});

export default router;
