<template>
  <div class="container mt-4">
    <h1 class="text-center mb-4">Lista de Tareas</h1>
    <div class="d-flex justify-content-between mb-4">
      <button class="btn btn-primary" @click="goToAgregar">Agregar Tarea</button>
      <input
        type="text"
        class="form-control w-50"
        placeholder="Buscar tareas"
        v-model="searchQuery"
        @input="fetchFilteredTasks"
      />
    </div>
    <table class="table table-bordered">
      <thead>
        <tr>
          <th>Título</th>
          <th>Descripción</th>
          <th>Estado</th>
          <th>Acciones</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="task in tasks" :key="task.id">
          <td>{{ task.title }}</td>
          <td>{{ task.description }}</td>
          <td>{{ task.completada ? 'Completada' : 'Pendiente' }}</td>
          <td>
            <button class="btn btn-info mr-2" @click="goToEditar(task.id)">Editar</button>
            <button class="btn btn-danger mr-2" @click="deleteTask(task.id)">Eliminar</button>
            <button
              class="btn"
              :class="task.completada ? 'btn-warning' : 'btn-success'"
              @click="confirmCompleteTask(task.id, task.completada)"
            >
              {{ task.completada ? 'Pendiente' : 'Completada' }}
            </button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import api from '../services/api';

export default {
  name: 'ListarTareasComponent',
  data() {
    return {
      tasks: [],
      searchQuery: ''
    };
  },
  created() {
    this.fetchTasks();
  },
  methods: {
    async fetchTasks() {
      const response = await api.get('/tasks');
      this.tasks = response.data;
    },
    async fetchFilteredTasks() {
      const response = await api.get('/tasks/filtrar', {
        params: { query: this.searchQuery }
      });
      this.tasks = response.data;
    },
    goToAgregar() {
      this.$router.push('/agregar');
    },
    goToEditar(id) {
      this.$router.push(`/editar/${id}`);
    },
    async deleteTask(id) {
      await api.delete(`/tasks/${id}`);
      this.fetchFilteredTasks();
    },
    async completeTask(id) {
      await api.put(`/tasks/${id}/completada`);
      this.fetchFilteredTasks();
    },
    confirmCompleteTask(id, completada) {
      const message = completada
        ? "¿Está seguro de que desea marcar esta tarea como pendiente?"
        : "¿Está seguro de que desea marcar esta tarea como completada?";
      if (confirm(message)) {
        this.completeTask(id);
      }
    }
  }
};
</script>

<style scoped>
.container {
  max-width: 800px;
  margin-top: 20px;
}
</style>
