<!-- components/AgregarComponent.vue -->
<template>
  <div class="container mt-4">
    <h1 class="text-center mb-4">Agregar Tarea</h1>
    <form @submit.prevent="addTask" class="mb-4">
      <div class="form-group">
        <label for="title">Título</label>
        <input type="text" v-model="title" class="form-control" id="title" required>
      </div>
      <div class="form-group">
        <label for="description">Descripción</label>
        <textarea v-model="description" class="form-control" id="description"></textarea>
      </div>
      <button type="submit" class="btn btn-primary">Guardar</button>
    </form>
    <router-link to="/tareas" class="btn btn-secondary">Volver al Listado de Tareas</router-link>
  </div>
</template>

<script>
import api from '../services/api';

export default {
  name: 'AgregarComponent',
  data() {
    return {
      title: '',
      description: ''
    };
  },
  methods: {
    async addTask() {
      await api.post('/tasks', {
        title: this.title,
        description: this.description
      });
      this.$router.push('/tareas');
    }
  }
};
</script>

<style scoped>
.container {
  max-width: 600px;
}
</style>
