<!-- components/EditarComponent.vue -->
<template>
  <div class="container mt-4">
    <h1 class="text-center mb-4">Editar Tarea</h1>
    <form @submit.prevent="updateTask" class="mb-4">
      <div class="form-group">
        <label for="title">Título</label>
        <input type="text" v-model="title" class="form-control" id="title" required>
      </div>
      <div class="form-group">
        <label for="description">Descripción</label>
        <textarea v-model="description" class="form-control" id="description"></textarea>
      </div>
      <button type="submit" class="btn btn-primary">Guardar Cambios</button>
    </form>
    <router-link to="/tareas" class="btn btn-secondary">Volver al Listado de Tareas</router-link>
  </div>
</template>

<script>
import api from '../services/api';

export default {
  name: 'EditarComponent',
  data() {
    return {
      title: '',
      description: ''
    };
  },
  created() {
    this.fetchTask();
  },
  methods: {
    async fetchTask() {
      try {
        const id = this.$route.params.id;
        const response = await api.get(`/tasks/${id}`);
        this.title = response.data.title;
        this.description = response.data.description;
      } catch (error) {
        console.error('Error fetching task:', error);
      }
    },
    async updateTask() {
      try {
        const id = this.$route.params.id;
        await api.put(`/tasks/${id}`, {
          title: this.title,
          description: this.description
        });
        this.$router.push('/tareas');
      } catch (error) {
        console.error('Error updating task:', error);
      }
    }
  }
};
</script>

<style scoped>
.container {
  max-width: 600px;
}
</style>
