nav {
  display: flex;
  justify-content: space-around;
  background-color: #333;
  padding: 10px;
}
nav a, nav button {
  color: white;
  text-decoration: none;
}
nav a:hover, nav button:hover {
  text-decoration: underline;
}
nav button {
  background: none;
  border: none;
  cursor: pointer;
}
.main-content {
  display: flex;
  flex-direction: column;
  align-items: center;
